import { Directive, Inject } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, Validator } from '@angular/forms';
import { isValidIBANFormControl } from './index';
import { BB_CONTACT_IBAN_COUNTRIES_LIST } from '../../config.providers';
import { addIBANCountries, IBANCountryList } from './countrySpec';

/**
 * This is a directive to validate iban
 *
 * @usageNotes
 *
 * ### validates the iban
 *
 * ```html
 * <input type="text" #ibanControl="ngModel" [(ngModel)]="iban" bbIbanValidatorUi />
 * ```
 * @ngModule IbanValidatorModule
 */

@Directive({
  selector: '[bbIbanValidator]',
  providers: [{ provide: NG_VALIDATORS, useExisting: IbanValidatorDirective, multi: true }],
})
export class IbanValidatorDirective implements Validator {
  constructor(@Inject(BB_CONTACT_IBAN_COUNTRIES_LIST) private readonly ibanCountryList: IBANCountryList) {
    addIBANCountries(this.ibanCountryList);
  }

  validate(control: AbstractControl): { [key: string]: any } | null {
    return control && control.value ? isValidIBANFormControl(control) : null;
  }
}
